import styled from "styled-components/macro";
export const Container=styled.div` display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
margin: auto;
max-width: 80%;
`;
export const List=styled.ul`display: flex;
flex-direction: row;
margin: 0;
padding: 0;`;
export const Item=styled.li`
width: 200px;
height: 200px;
list-style-type: none;
margin-right: 30px;
text-align: center;
&:hover>img{
    border-color: white;
}
&:hover>p{
    font-weight: bold;
    color: #e5e5e5;
}
&:
:last-of-type{
    margin-right:0px;
}
`;
export const Picture=styled.img`width: 100%;
max-width: 150px;
cursor: pointer;
height: auto;
border: 3px solid black;

`;
export const Name=styled.p` font-size: 16px;
text-overflow: ellipsis;
color: #808080;

`;
export const Titel=styled.h1`width: 100%;
text-align: center;
font-size: 45px;
color: white;
font-weight: 500;`;
