import React from "react";
import { Container, Titel, List, Picture, Name,Item } from "./styles/profile";

export default function Profiles({ children, ...restprops }) {
  return <Container {...restprops}>{children}</Container>;
}

Profiles.Titel = function ProfilesTitel({ children, ...restprops }) {
  return <Titel {...restprops}>{children}</Titel>;
};

Profiles.List = function ProfilesList({ children, ...restprops }) {
  return <List {...restprops}>{children}</List>;
};


Profiles.Picture = function ProfilesPicture({src , ... restprops }) {
  return <Picture  src={src ?'/images/users/'+src+'.png' : 'images/misc/loading.gif'}
  {... restprops} 
   />;
};
Profiles.Name = function ProfilesName({ children, ...restprops }) {
  return <Name {...restprops}>{children}</Name>;
};
Profiles.Item = function ProfilesItem({ children, ...restprops }) {
    return <Item {...restprops}>{children}</Item>;
  };
