import React, { useContext, createContext, useState } from "react";
import {
  Title,
  Container,
  Inner,
   Item,
  Header,
  Body,
} from "./styles/accordion";
const ToggelContext = createContext();
function Accordion({ children, ...restProps }) {
  return (
    <Container {...restProps}>
      <Inner>{children}</Inner>
    </Container>
  );
}
Accordion.Title = function AccordionTitle({ children, ...restProps }) {
  return <Title {...restProps}>{children}</Title>;
};

Accordion.Item = function AccordionItem({ children, ...restProps }) {
  const [ToggoleShow, SetToggoleShow] = useState(false);

  return (
    <ToggelContext.Provider value={{ ToggoleShow, SetToggoleShow }}>
      <Item {...restProps}>{children}</Item>
    </ToggelContext.Provider>
  );
};
Accordion.Header = function AccordionHeader({ children, ...restProps }) {
  const { ToggoleShow, SetToggoleShow } = useContext(ToggelContext);

  return (
    <Header  onClick={() => SetToggoleShow((ToggoleShow) => !ToggoleShow)} {...restProps}>
      {children}
      {ToggoleShow ?(<img src="/images/icons/close-slim.png" alt="Close"/>):(<img src="/images/icons/add.png" alt="Open" />)}
    </Header>
  );
};
Accordion.Body = function AccordionBody({ children, ...restProps }) {
  const { ToggoleShow } = useContext(ToggelContext);
  return ToggoleShow ? <Body {...restProps}>{children}</Body> : null;
};

export default Accordion;
