import styled from "styled-components/macro";
export const Container=styled.div`
display: flex;
border-bottom: 8px solid #222;
`;
export const Inner=styled.div`  display: flex;
flex-direction: column;
padding: 70px 45px;
max-width: 815px;
margin: auto;`;
export const Title=styled.h1`
font-size: 50px;
margin-top: 0;
text-align: center;
margin-bottom: 8px;
color: white;
line-height: 1.1;
@media(max-width:1000px){
    font-size: 35px;
}
`;
export const Item=styled.div` color: white;
margin-bottom: 10px;
max-width:700px;
width:100%;
margin:auto;
&:first-of-type{
   margin-top: 3em;
}`;
export const Header=styled.div`display: flex;
justify-content: space-between;
font-weight: normal;
background-color: #303030;
padding: .8em 1.2em;
font-size: 26px;
cursor: pointer;
margin-bottom: 1px;
user-select: none;
align-items: center;
img {
    filter: brightness(0) invert(1);
width: 24px;
@media (max-width:600px){
    width: 16px;
}
@media (max-width:600px){
    font-size: 16px;
}
} 

`;
export const Body=styled.div`max-height: 1200px;
transition: max-height 0.25s cubic-bezier(0.5,0,0.1, 1);
font-weight: normal;
line-height: normal;
font-size: 26px;
background-color: #303030;
user-select: none;
white-space: pre-wrap;
padding: .8em 2.2em .8em 1.2em;

@media (max-width:600px){
    font-size: 16px;
    line-height:22px
}
`;
