import React from "react";
import { useState } from "react";
import { Link as ReactRouterLink } from "react-router-dom";
import {
  Frame,
  Background,
  Logo,
  ButtonLink,
  Container,
  Feutre,
  Text,
  BIGText,
  TextLink,
  Group,
  Picture,
  Profile,
  Dropdown,
  Search,
  SarchIcon,
  SearchInput,
  PlayButton,
} from "./styles/header";
function Header({ bg = true, children, ...restProps }) {
  return bg ? <Background {...restProps}>{children}</Background> : children;
}
Header.Frame = function HeaderFrame({ children, ...restProps }) {
  return (
    <Container {...restProps}>
      <Frame>{children}</Frame>
    </Container>
  );
};

Header.Text = function HeaderText({ children, ...restProps }) {
  return <Text {...restProps}>{children}</Text>;
};
Header.PlayButton = function HeaderPlayButton({ children, ...restProps }) {
  return <PlayButton {...restProps}>{children}</PlayButton>;
};

Header.Search = function HeaderSearch({ Serchterm,setSerchterm, ... restProps }) {
const [Activesearch ,setActivesearch]=useState(false);

  return <Search {... restProps}>
<SarchIcon onClick={()=> setActivesearch((Activesearch) =>!Activesearch)}>
  <img  src="images/icons/search.png" alt="search"/>
</SarchIcon>
<SearchInput value={Serchterm}
onChange={({target})=>setSerchterm(target.value)}
placeholder='Search films and series'
active={Activesearch}
/>
  </Search>;
};

Header.Dropdown = function HeaderDropdown({ children, ...restProps }) {
  return <Dropdown {...restProps}>{children}</Dropdown>;
};
Header.Profile = function HeaderProfile({ children, ...restProps }) {
  return <Profile {...restProps}>{children}</Profile>;
};
Header.Group = function HeaderGroup({ children, ...restProps }) {
  return <Group {...restProps}>{children}</Group>;
};
Header.TextLink = function HeaderTextLink({ children, ...restProps }) {
  return <TextLink {...restProps}>{children}</TextLink>;
};
Header.BIGText = function HeaderBIGText({ children, ...restProps }) {
  return <BIGText {...restProps}>{children}</BIGText>;
};
Header.ButtonLink = function HeaderButtonLink({ children, ...restProps }) {
  return <ButtonLink {...restProps}>{children}</ButtonLink>;
};
Header.Feutre = function HeaderFeutre({ children, ...restProps }) {
  return <Feutre {...restProps}>{children}</Feutre>;
};
Header.Picture = function HeaderPicture({ src, ...restprops }) {
  return (
    <Picture
      src={src ? "/images/users/" + src + ".png" : "images/misc/loading.gif"}
      {...restprops}
    />
  );
};
Header.Logo = function HeaderLogo({ to, ...restProps }) {
  return (
    <ReactRouterLink to={to}>
      <Logo {...restProps} />
    </ReactRouterLink>
  );
};

export default Header;
