import React ,{useState,useContext} from "react";

require('firebase/auth');
import { useHistory } from "react-router-dom";
import HeaderContainer from '../container/header';
import {FirebaseContext} from '../context/firebase';
import FooterContainer from '../container/footer';
import {Form} from'../components';
import * as ROUTES from "../components/constants/route";

function SIGEN_UP(){
    const hestory =useHistory();
    const { firebase }=useContext(FirebaseContext);

    const [firstname,setfirstname]=useState('');
        const [emailadress,setemailadress]=useState('');
const [password,setpassword]=useState('');
const [error,seterror]=useState('');
const Invalid= firstname==''||password=='' ||emailadress=='';
const Hanlersignup =(event)=>{
    event.preventDefault();

firebase
.auth()
.createUserWithEmailAndPassword(emailadress,password)
.then((result)=>{

    result.user
    .updateProfile({

        displayName:firstname,
        photoURL:Math.floor(Math.random()*5)+1
    })
}).then(()=>{
    hestory.push(ROUTES.BROWSER)
    })

    .catch((error)=>{
        setfirstname('');
        setemailadress('');
        setpassword('');
        seterror(error.message);
        });
}
return(
    <>
    <HeaderContainer>
        <Form><Form.Titel>Sign up</Form.Titel>
        {error &&<Form.Error>{error}</Form.Error>}
        <Form.Base onSubmit={Hanlersignup} method="POST">
        <Form.Input  placeholder="Enter your First name "  value={firstname}
            onChange={({target})=>setfirstname(target.value)}
            />
            <Form.Input  placeholder="Enter your email" type="email" value={emailadress}
            onChange={({target})=>setemailadress(target.value)}
            />
               <Form.Input type='password' autoComplete="off" value={password} placeholder='Enter Passord'
            onChange={({target})=>setpassword(target.value)}
            />
<Form.Submet disabled={Invalid} type='submet'>Sign In</Form.Submet>
            </Form.Base> 
            <Form.Text>Already a use? <Form.Link to="/sigenin">Sign in now.</Form.Link></Form.Text>
            <Form.Smalltext>This page is prodected by google reCAPTCHA to ensure you`re not not a bot.Learn more.</Form.Smalltext>
        </Form>
    </HeaderContainer>
<FooterContainer/>
</>
)

}
export default SIGEN_UP;