import React ,{useState,useContext,createContext} from "react";
import ReactDOM  from "react-dom";
import {Container,Inner,Overlay,Button} from "./styles/player"

export const PlayerContxt=createContext();
function player ({children,...restProps}){
const [PlayerShow,setPlayerShow]=useState(false);
return (<PlayerContxt.Provider value={{PlayerShow,setPlayerShow}}>
    <Container {...restProps}>
{children}
    </Container>
</PlayerContxt.Provider>)
}
player.Video=function PlayerVideo({src,...restProps}){
    const {PlayerShow,setPlayerShow}=useContext(PlayerContxt);
    PlayerShow;
return PlayerShow? ReactDOM.createPortal(
<Overlay onClick={()=>setPlayerShow(false)}  {...restProps}>
    <Inner>
    <video id="netflix-player" controls>
        <source src={src} type="video/mp4"/>
        </video> 
               </Inner>
</Overlay>,
document.body
):null

}
player.Button=function PlayerButton ({...restProps}){
    const {PlayerShow,setPlayerShow}=useContext(PlayerContxt);
    PlayerShow;
    return <Button {...restProps} onClick={()=>setPlayerShow(PlayerShow =>!PlayerShow)}>Player</Button>
}


export default player;
