import styled from "styled-components";
import { Link as ReactRouterLink } from "react-router-dom";
export const Container = styled.div`
  display: flex;
  margin: 0 56px;
  padding: 18px 0;
  justify-content: space-between;
  align-items: center;
  height: 64px;
  a {
    display: flex;
    justify-content: space-between;
  }
  @media (max-width: 1499px) {
    margin: 0 30px;
  }
`;
export const Background = styled.div`
  display: flex;
  flex-direction: column;
  background-image: url(${({ src }) =>
    src ? "../images/misc/" + src + ".jpg" : "../images/misc/home-bg.jpg"});
  background-size: cover;
  @media (max-width: 1100px) {
    ${({ DontShowsmallPort }) => DontShowsmallPort && " background: none;"}
  }
`;
export const Group = styled.div`
  display: flex;
  align-items: center;
`;
export const Frame = styled.div`
  display: contents;
`;
export const Logo = styled.img`
  margin-right: 40px;
  height: 32px;
  width: 108px;

  @media (max-width: 1499px) {
    height: 45px;
    width: 167px;
  }
`;
export const ButtonLink = styled(ReactRouterLink)`
  display: block;
  background-color: #e50914;
  height: fit-content;
  width: 90px;
  color: white;
  border: 0;
  font-size: 15px;
  box-sizing: border-box;
  cursor: pointer;
  padding: 8px 17px;
  border-radius: 3px;
  text-decoration: none;
  &:hover {
    background: #f40612;
  }
`;
export const Feutre = styled(Container)`
  padding: 150px 0 500px 0;
  flex-direction: column;
  align-items: normal;
  width: 50%;

  @media (max-width: 1100px) {
    display: none;
  }
`;

export const Text = styled.p`
  font-size: 22px;
  line-height: normal;
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.45);
  margin: 0;
`;
export const TextLink = styled.p`
  text-decoration: none;
  margin-right: 30px;
  color: white;
  cursor: pointer;
  font-weight: ${({ active }) => (active == "true" ? "700" : "normal")};
  &:hover {
    font-weight: bold;
  }

  &:last-of-type {
    margin-right: 0;
  }
`;
export const BIGText = styled.h1`
  font-size: 50px;
  line-height: normal;
  font-weight: bold;
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.45);
  margin: 0;
  margin-bottom: 20px;
`;
export const Profile = styled.div`
  display: flex;
  align-items: center;
  margin-left: 20px;
  position: relative;
  button {
    cursor: pointer;
  }
  &:hover > div {
    display: flex;
    flex-direction: column;
  }
`;

export const Picture = styled.button`
  background: url(${({ src }) => src});

  background-size: contain;
  width: 32px;
  height: 32px;
  border: 0;
  cursor: pointer;
`;

export const Dropdown = styled.div`
  display: none;

  background-color: black;
  width: 100px;
  padding: 10px;
  position: absolute;
  top: 32px;
  right: 10px;
  ${Group}:last-of-type ${TextLink} {
    cursor: pointer;
  }

  ${Group} {
    margin-bottom: 10px;
    &:last-of-type {
      margin-bottom: 0;
    }
    ${TextLink} ,${Picture} {
      cursor: default;
    }
    button {
      margin-right: 10px;
    }
    p {
      margin-bottom: 0;
      font-size: 12px;

      margin-top: 0;
    }
  }
`;

export const Search = styled.div`
  display: flex;
  align-items: center;

  svg {
    color: white;
    cursor: pointer;
  }
  @media (max-width: 700px) {
    display: none;
  }
`;
export const SarchIcon = styled.button`
  cursor: pointer;
  background-color: transparent;
  border: 0;
  img {
    width: 16px;
    filter: brightness(0) invert(1);
  }
`;
export const SearchInput = styled.input`
  background-color: #44444459;
  border: 1px solid white;
  color: white;
  transition: width 0.5s;
  height: 30px;
  font-size: 14px;
  margin-left: ${({ active }) => (active == true ? "10px" : "0")};
  padding: ${({ active }) => (active == true ? "0 10px" : "0")};
  opacity: ${({ active }) => (active == true ? "1" : "0")};
  width: ${({ active }) => (active == true ? "200px" : "0")};
`;

export const PlayButton=styled.button`
font-weight: bold;
box-shadow: 0 0.6vw -0.4vw rgba(0,0,0,.35);
background-color: #e6e6e6;
color:#000;
border-width: 0;
padding: 10px 20px;
border-radius: 5px;
max-width: 130px;
font-size: 20px;
margin-top: 30px;
transition: background-color 0.2s;
cursor: pointer;
&:hover{
    background-color: #ff1e1e;
color:white
}

`;

