import React, { useState , createContext, useContext } from "react";
import {
  Container,
  Text,
  Titel,
  Subtitel,
  Group,
  Meta,
  Entites,
  Feater,
  Content,
  FeaterTitel,
  FeaterClose,
  FeaterText,
  Matiurty,
  Item,
  Image,
} from "./styles/card";

export const FeaterConstent = createContext();

export default function Card({ children, ...restprops }) {
  const [showFeater, setshowFeater] = useState(false);
  const [itemFeater, setitemFeater] = useState({});
  return (
    <FeaterConstent.Provider
      value={{ showFeater, setshowFeater, itemFeater, setitemFeater }}
    >
      <Container {...restprops}>{children}</Container>
    </FeaterConstent.Provider>
  );
}

Card.Group = function CardGroup({ children, ...restprops }) {
  return <Group {...restprops}>{children}</Group>;
};

Card.Titel = function CardTitel({ children, ...restprops }) {
  return <Titel {...restprops}>{children}</Titel>;
};

Card.Subtitel = function CardSubtitel({ children, ...restprops }) {
  return <Subtitel {...restprops}>{children}</Subtitel>;
};
Card.Text = function CardText({ children, ...restprops }) {
  return <Text {...restprops}>{children}</Text>;
};
Card.Meta = function CardMeta({ children, ...restprops }) {
  return <Meta {...restprops}>{children}</Meta>;
};

Card.Feater = function CardFeater({children,catgory,...restprops }) {
  const {showFeater, setshowFeater,itemFeater}=useContext(FeaterConstent);
  return showFeater?(<Feater {...restprops} src={`/images/${catgory}/${itemFeater.genre}/${itemFeater.slug}/large.jpg`}>
    <Content>
      <FeaterTitel>{itemFeater.title}</FeaterTitel>
<FeaterText>{itemFeater.description}</FeaterText>
<FeaterClose onClick={()=> setshowFeater(false)}>
  <img src='/images/icons/close.png' alt="close"/>
</FeaterClose>
  
    <Group margin= "30px 0" flexDirection="row" alignItems="center">
      <Matiurty rating={itemFeater.maturity}>
        {itemFeater.maturity<12 ? "PG":itemFeater.maturity}
              </Matiurty>
              <FeaterText fontWeight="normal">
                {itemFeater.genre.charAt(0).toUpperCase()+itemFeater.genre.slice(1)}
              </FeaterText>
    </Group>
    {children}
    </Content>
  </Feater>):null;
};

Card.Item = function CardItem({ children,item,  ... restprops }) {
  const {setitemFeater , setshowFeater}=useContext(FeaterConstent);

  return (
    <Item
      onClick={() => {
        setitemFeater(item);
        setshowFeater(true);
      }}
      {... restprops}
    >
      {children}
    </Item>
  );
};

Card.Entites = function CardEntites({ children, ...restprops }) {
  return <Entites {...restprops}>{children}</Entites>;
};
Card.Image = function CardImage({ ...restprops }) {
  return <Image {...restprops} />;
};


Card.Content = function CardContent({ children, ...restprops }) {
  return <Content {...restprops}>{children}</Content>;
};

Card.FeaterConstent = function CardFeaterConstent({ children, ...restprops }) {
  return <FeaterConstent {...restprops}>{children}</FeaterConstent>;
};


Card.Matiurty = function CardMatiurty({ children, ...restprops }) {
  return <Matiurty {...restprops}>{children}</Matiurty>;
};


