import React, { useContext, useState, useEffect } from "react";
import SelectProfileContainer from "./selictionfilecontaner";
import { FirebaseContext } from "../context/firebase";
import { Loading, Header, Card, Player } from "../components";
import FooterContainer from "./footer";
import logo from "../logo.svg";
import Fuse from "fuse.js";
import * as ROUTES from "../components/constants/route";

export default function BrowserContainer({ slider }) {
  const [catgory, setcatgory] = useState("series");
  const [Serchterm, setSerchterm] = useState("");
  const [profile, setProfile] = useState({});

  const [loading, setloading] = useState(true);
  const { firebase } = useContext(FirebaseContext);
  const [slideRow, setslideRow] = useState([]);
  const user = firebase.auth().currentUser || {};
  useEffect(() => {
    setTimeout(() => {
      setloading(false);
    }, 3000);
  }, [profile.displayName]);

  useEffect(() => {
    setslideRow(slider[catgory]);
  }, [slider, catgory]);
  useEffect(() => {
    const fuse = new Fuse(slideRow, {
      keys: ["data.description.", "data.title", "data.genre"],
    });
    const results = fuse.search(Serchterm).map(({ item }) => item);
    if (slideRow.length > 0 && Serchterm.length > 3 && results.length > 0) {
      setslideRow(results);
    } else {
      setslideRow(slider[catgory]);
    }
  }, [Serchterm]);

  return profile.displayName ? (
    <>
      {loading ? <Loading src={user.photoURL} /> : <Loading.ReleaseBody />}
      <Header src="joker1" DontShowsmallPort>
        <Header.Frame>
          <Header.Group>
            <Header.Logo to={ROUTES.HOME} src={logo} alt="Netflix" />
            <Header.TextLink
              active={catgory == "series" ? "true" : "false"}
              onClick={() => setcatgory("series")}
            >
              Series
            </Header.TextLink>
            <Header.TextLink
              active={catgory == "films" ? "true" : "false"}
              onClick={() => setcatgory("films")}
            >
              Films
            </Header.TextLink>
          </Header.Group>
          <Header.Group>
            <Header.Search setSerchterm={setSerchterm} Serchterm={Serchterm} />
            <Header.Profile>
              <Header.Picture src={user.photoURL} />
              <Header.Dropdown>
                <Header.Group>
                  <Header.Picture src={user.photoURL} />
                  <Header.TextLink>{user.displayName}</Header.TextLink>
                </Header.Group>
                <Header.Group>
                  <Header.TextLink
                    onClick={() => {
                      firebase.auth().signOut();
                    }}
                  >
                    Sign Out
                  </Header.TextLink>
                </Header.Group>
              </Header.Dropdown>
            </Header.Profile>
          </Header.Group>
        </Header.Frame>
        <Header.Feutre>
          <Header.BIGText>Watch Joker Now</Header.BIGText>
          <Header.Text>
            Forever alone in a crowd, failed comedian Arthur Fleck seeks
            connection as he walks the streets of Gotham City. Arthur wears two
            masks -- the one he paints for his day job as a clown, and the guise
            he projects in a futile attempt to feel like he is part of the world
            around him.
          </Header.Text>
          <Header.PlayButton>Play</Header.PlayButton>
        </Header.Feutre>
      </Header>
      <Card.Group>
        {slideRow.map((slideItem) => (
          <Card key={`${catgory}-${slideItem.title.toLowerCase()}`}>
            <Card.Titel>{slideItem.title}</Card.Titel>
            <Card.Entites>
              {slideItem.data.map((item) => (
                <Card.Item key={item.docId} item={item}>
                  <Card.Image
                    src={`/images/${catgory}/${item.genre}/${item.slug}/small.jpg`}
                  />
                  <Card.Meta>
                    <Card.Text>{item.title}</Card.Text>
                    <Card.Subtitel>{item.description}</Card.Subtitel>
                  </Card.Meta>
                </Card.Item>
              ))}
            </Card.Entites>
            <Card.Feater catgory={catgory}>
              <Player>
                <Player.Button />
                <Player.Video src="videos/bunny.mp4" />
              </Player>
            </Card.Feater>
          </Card>
        ))}
      </Card.Group>
      <FooterContainer></FooterContainer>
    </>
  ) : (
    <SelectProfileContainer user={user} setProfile={setProfile} />
  );
}
