import React from "react";
import { HOME, BROWSER, SIGEN_IN, SIGEN_UP } from "./Pages";
import * as ROUTES from "./components/constants/route";
import { BrowserRouter as Router, Switch } from "react-router-dom";
import {IsuserRedirect ,ProtectedRedirect} from "./helper/route";
import { UseAuthListenir} from'./hokes'
function App() {
  const  user  =UseAuthListenir();
  
  return (
    <Router>
       <Switch>
            <IsuserRedirect 
        user={user}
loggedInPath={ROUTES.BROWSER}
path={ROUTES.SIGEN_IN}
exact
>
          <SIGEN_IN />
        </IsuserRedirect>  
        <IsuserRedirect 
        user={user}
loggedInPath={ROUTES.BROWSER}
path={ROUTES.SIGEN_UP}
exact
>
        <SIGEN_UP />
        </IsuserRedirect>   
       <ProtectedRedirect user={user} path={ROUTES.BROWSER} exact>
       <BROWSER />
     </ProtectedRedirect>
     <IsuserRedirect 
        user={user}
loggedInPath={ROUTES.BROWSER}
path={ROUTES.HOME}
exact
>
        <HOME />
        </IsuserRedirect>
      </Switch>
    </Router>
  );
}

export default App;
