import React from "react";
import {Container ,Titel ,SubTitel} from './styles/featur';
function Featur({children ,...restProps}){
    return(<Container {...restProps}>{children}</Container>)
}

Featur.Titel=function FeaturTitel ({children,...restProps}){
    return (<Titel {...restProps}>{children}</Titel>)
}

Featur.SubTitel=function FeaturSubTitel ({children,...restProps}){
    return (<SubTitel {...restProps}>{children}</SubTitel>)
}

export default Featur;