import styled from "styled-components/macro";
 
export const Item=styled.div`
display:flex;
border-bottom: 2px solid #222;
padding: 50px 5%;
color: white;
overflow: hidden;
`
export const Inner = styled.div`
display:flex;
align-items: center;
flex-direction:${({direction}) =>direction} ;
justify-content: space-between;
max-width: 1100px;
    margin:auto;
    width:100%;

    @media (max-width:1000px){
        flex-direction: column;
    }

`;
export const Container=styled.div`
@media (max-width:1000px){
 ${Item}:last-of-type h2{
  margin-bottom: 50px;
 }
}
`;

export const Pang=styled.div`
width:50%;
padding:10px;
@media (max-width:1000px){
   width:100%;
   padding:  0 45px;
   text-align: center;
}
`;

export const Title=styled.h1`
font-size: 50px;
line-height:1.1;
margin-bottom: 8px;
@media (max-width:1000px){
    font-size:35px
  }
`;
export const SubTitel=styled.h2`  font-size: 26px;
font-weight: normal;
line-height: normal;
@media (max-width:1000px){
   font-size:18px
 }`;
export const Image=styled.img` 
 width:50%;
height: auto;`;