import styled from "styled-components";
export const Titel = styled.p`
  font-size: 24px;
  color: #e5e5e5;
  font-weight: bold;
  margin-left: 56px;
  margin-right: 56px;
  margin-top: 0;
`;
export const Container = styled.div`
  display: flex;
  flex-direction: column;
  margin-bottom: 50px;
  box-sizing: border-box;
  > ${Titel} {
    @media (max-width: 1000px) {
      margin-left: 30px;
    }
    &:last-of-type {
      margin-bottom: 0;
    }
  }
`;
export const Group = styled.div`
  display: flex;
  flex-direction: ${({ flexDirection }) =>
    flexDirection == "row" ? "row" : "column"};
   ${({ alignItems }) => alignItems && `align-items:${alignItems}`};
  ${({ margin }) => margin && `margin:${margin}`};
  > ${Container}:first-of-type {
    @media (min-width: 1100px) {
      margin-top: -150px;
    }
  }
`;

export const Subtitel = styled.p`
  font-size: 12px;
  font-weight: bold;
  line-height: normal;
  margin-bottom: 0;
  display: none;
  user-select: none;
  margin-top: 0;
  color: #fff;
`;
export const Text = styled.p`
  font-size: 10px;
  line-height: normal;
  margin-bottom: 5px;
  display: none;
  user-select: none;
  margin-top: 5px;
  color: #fff;
`;

export const FeaterText = styled.p`
  font-size: 18px;
  color: white;
  font-weight: ${({ fontWeight }) =>
    fontWeight == "bold" ? "bold" : "normal"};
    margin:0;
    
@media (max-width:600px){
    line-height: 22px;
}
`;
export const Feater = styled.div`
  background: url(${({ src }) => src});
  position: relative;
  display: flex;
  flex-direction: column;
  height: 360px;
  background-position-x: right;
  position: relative;
  background-size: contain;
  background-repeat: no-repeat;
  background-color: black;

  @media (max-width: 1000px) {
    height: auto;
    background-size: auto;
    ${Titel} {
      font-size: 20px;
      line-height: 20px;
      margin-bottom: 10px;
    }

    ${FeaterText} {
      font-size: 14px;
    }
  }
`;
export const FeaterTitel = styled(Titel)`
  margin-left: 0;
`;
export const FeaterClose = styled.button`
  cursor: pointer;
  color: white;
  position: absolute;
  top: 20px;
  right: 20px;
  background-color: transparent;
  border: 0;

  img {
    width: 24px;
    filter: brightness(0) invert(1);
  }
`;
export const Matiurty = styled.div`
  background-color: ${({ rating }) => (rating >= 15 ? "red" : "green")};
  
  border-radius: 15px;
  color: white;
  padding: 5px;
  width: 20px;
  text-align: center;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.2);
  margin-right: 10px;
  font-size: 15px;
`;
export const Meta = styled.div`
  display: none;
  position: absolute;
  bottom: 0;
  padding: 10px;
  background-color: #0000008f;
`;
export const Content = styled.div`
  margin: 56px;
  max-width: 500px;
  line-height: normal;

  @media (max-width: 1000px) {
    margin: 30px;
    max-width: none;
  }
`;
export const Item = styled.div`
display: flex;
flex-direction: column;
margin-right: 5px;
position: relative;
cursor: pointer;
transition: transform 0.2s;
&:hover{
    transform: scale(1.3);
z-index: 99;
}

@media (min-width:1200px){
&:hover ${Meta} ,&:hover ${Text},&:hover ${Subtitel}{
    
  display: block;
  z-index: 100;
}}
$:first-of-type{
    margin-left:56px;

    @media (max-width:1000px){
margin-left:30px;
    }   

}

$:last-of-type{
    margin-right:56px;

    @media (max-width:1000px){
margin-right:30px;
    }   

}


`;
export const Entites = styled.div`
  display: flex;
  flex-direction: row;
`;
export const Image = styled.img`
  border: 0;
  width: 100%;
  max-width: 305px;
  cursor: pointer;
  padding: 0;
  margin: 0;
  height: auto;
`;
