import React from "react";
import { Inner,Container,Item ,Pang,Title,SubTitel,Image } from "./styles/jumbotrono";

function Jumbotron({children , direction='row' ,...restProps}) {
    return(
    <Item {...restProps}>
        <Inner  direction={direction}>
   {children}
   </Inner>
    </Item>
    )
}
 Jumbotron.Container=function JumbotornContainer({children,...restProps}){
     return(<Container {...restProps}>{children}</Container> )
 }
 Jumbotron.Title=function JumbotronTitle({children,...restProps}){
    return(<Title {...restProps}>{children}</Title> )
}
Jumbotron.SubTitel=function JumbotornSubTitel({children,...restProps}){
    return(<SubTitel {...restProps}>{children}</SubTitel> )
}
Jumbotron.Image=function JumbotornImage({...restProps}){
    return(<Image {...restProps}/> )
}
Jumbotron.Pang=function JumbotornPang({children,...restProps}){
    return(<Pang {...restProps}>{children}</Pang> )
}
export default Jumbotron;