import styled from "styled-components/macro";
import { Link as ReactRouterLink } from "react-router-dom";
export const  Container=styled.div`
display: flex;
flex-direction: column;
min-height: 660px;
max-width: 100%;
margin: auto;
width: 450px;
padding: 60px 68px 40px;
box-sizing: border-box;
margin-bottom: 100px;
border-radius: 5px;
background-color: rgba(0, 0, 0,0.75);
`;
export const  Base=styled.form`display: flex;
flex-direction: column;
max-width: 450px;
width:100%;
`;
export const  Error=styled.div`
background-color: #e87c03;
border-radius: 4px;

font-size: 14px;
margin: 0 0 16px;
color: white;
padding: 15px 20px;

`;
export const  Titel=styled.h1`
color: #fff;
font-size: 32px;
font-weight: bold;
margin-bottom: 28px;
`;
export const  Submet=styled.button`
background-color: #e50914;
border-radius: 4px;
font-size: 16px;
color: white;
font-weight: bold;
cursor: pointer;
border: 0;
padding: 16px;
margin: 24px 0 12px;

&:disabled{
    opacity: 0.5;
}
`;
export const  Text=styled.p`
color: #757575;
font-size: 16px;
font-weight:500;
`;
export const  Smalltext=styled.p`
color: #8c8c8c;
font-size: 13px;
line-height:normal;
margin-top: 10px;
`;
 export const  Link=styled(ReactRouterLink)`
text-decoration: none;
 color: white;
&:hover{
    text-decoration: underline;
}

`;
export const  Input=styled.input`
background-color: #333;
border-radius: 4px;
border: 0;
color: white;
height: 50px;
line-height: 50px;
padding: 5px 20px;
margin-bottom: 20px;

&:last-of-type{
    margin-bottom:30px;
}
`;