import styled from "styled-components/macro";

export const Container=styled.div`
display: flex;
flex-wrap: wrap;
justify-content: center;
margin-top: 20px;
height: 100%;
@media (max-width:1000px){
flex-direction: column;
align-items: center;
}
`;
export const Button=styled.button` padding: 0 32px;
color: white;
background-color: #e50914;
text-transform: uppercase;
align-items: center;
border: 0;
cursor: pointer;
 height: 70px;
 font-size: 26px;
 &:hover{
    background-color: #F40612;
 }
 @media (max-width:1000px){
    height: 50px;
    font-size: 16px;
    margin-top: 20px;
    font-weight: bold;
}
img{
    margin-left: 10px;
filter: brightness(0) invert(1);
width:24px;
@media (max-width:1000px){
 width:16px;
}

}

 `;
export const Input=styled.input` max-width: 450px;
width: 100%;
padding: 10px;
border: 0;
box-sizing: border-box;
height: 70px;`;
export const Text=styled.p`
font-size: 23px;
text-align: center;
color: white;
@media (max-width:1000px){
    font-size: 19.2px;
    line-height: 22px;
}
`;
export const Break=`display: flex;
flex-basis: 100%;
`;