import styled from "styled-components/macro";

export const Container =styled.div`
text-align: center;
color:white;
border-bottom: solid 8px #222;
display: flex;
flex-direction: column;
padding:90px 45px;
`
;
export const Titel =styled.h1`font-size: 50px;
max-width:640px;
margin: auto;
font-weight: 500;
@media (max-width:600px){
    font-size:35px
  }`;
export const SubTitel =styled.h3`font-size: 26px;

margin: 16px auto;
font-weight: normal;
@media (max-width:600px){
    font-size:18px
  }`;